.People {
            @media only screen and (max-width:999px) {
                position: relative;
            }
    h2 {
        padding-top: 30px;
        margin-top: 40px;
    }
    @media only screen and (min-width:1000px) { 
        .dropdownUI {
            display:none;
        }
        .tabNav {
            position:sticky; top:0;
            background: #f7f7f7;
            width:100%;
            width:auto;
            z-index:1;
            border-top: 1px solid #eaeaea;
            ul {
                display:flex;
                flex-direction:row;
                flex-wrap:wrap;
                align-items: center;
                justify-content: center;
                list-style-type: none;
                margin: 0 auto;
                padding: 0;
                
                a,
                button {
                    border: none;
                    background: none;
                    color: #3a3a3a;
                    text-decoration: none;
                    text-transform: uppercase;
                    li {
                        flex:0 0 auto;
                        padding: 12px 4px;
                        margin: 0 18px;
                        font-family: 'Courier New', Courier, monospace;
                        font-size:14px;
                        text-transform: uppercase;
                        border-top:3px solid transparent;
                        color:#707070;
                        cursor:pointer;
                    }
                    li:hover {
                        border-top:3px solid #F40019;
                    }
                    li.active {
                        color:#000;
                        font-weight:600;
                        border-top:3px solid #F40019;
                    }
                }
            
                
                
            }
        }
    }
    @media only screen and (max-width:999px) { 
        .dropdownUI {
            position: relative;
            font-family: 'Courier New', Courier, monospace;
            font-size: 18px;
            -moz-user-select: none;
            user-select: none;
            .dd-header {
                // width: 100%;
                padding: 8px 8px;
                position: relative;
                background: #F7F7F7;
    
                .dd-header-title {
                    padding: 6px 18px;
                    color: black;
                }
    
                .dd-header-arrow {
                    position: absolute;
                    top: 15px;
                    right: 15px;
                    color: red;
                }
    
                cursor:pointer;
            }
    
            ul.dd-list {
                position: absolute;
                z-index: 100;
                left: 0px;
                right: 0px;
                background: #222;
                box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.08);
                margin: 0px;
                padding: 16px 0;
                list-style-type: none;
    
                li {
                    padding: 12px 24px;
    
                    a {
                        text-decoration: none;
                        color: white;
                    }
    
                    a:hover {
                        color: #F40019;
                    }
                }
            }
        }
        .tabNav { display:none;}
    }
    
    
    .PageContentWrapper {
        @media only screen and (min-width:1000px) { margin: 80px auto;}
        @media only screen and (max-width:999px) { margin: 40px auto; }
        
        max-width: 1200px;
        .PageTitle {
            height: 54px;
            width: 100%;
            font-size: 32px;
            font-weight: 800;
        }
        .PageContent{
            font-size:60px;
        }
    }

    .PeopleViewer {
        width:100%;
        display:grid;
        @media only screen and (min-width:1000px) { 
            grid-template-columns: repeat(auto-fill, 200px);
            gap:100px;
        }
        @media only screen and (max-width:1000px) { 
            grid-template-columns: repeat(auto-fill, 140px);
            gap:40px;
        }
        
        // flex-direction: row;
        // flex-wrap: wrap;
        justify-content:space-around;
        box-sizing: border-box;
        // grid-gap:20px;
        padding:0 20px;
        
        // @media only screen and (min-width:1000px) {  gap:73px; }
        // @media only screen and (max-width:1000px) {  gap:40px; }
        .person {
            flex: 0 0 auto;
        }
    }
    
    .person {
        @media only screen and (min-width:1000px) { 
            width:200px; // height:370px;
            font-size:20px;
        }
        @media only screen and (max-width:1000px) { 
            width:140px; // height:370px;
            font-size:17px;
        }
        
        font-size:15px;
        // font-family: "Work sans", sans-serif;
        .photo {
            width:100%;
            overflow:hidden;
            img {
                width:100%; 
                aspect-ratio: 1;
                object-fit: cover;
                filter:saturate(0);
                transition: filter .5s;
            }
            img:hover {
                filter:none;
            }
        }
        .name_and_lab {
            display:block;
            .name {
                display:inline-block;
                font-size:1em;
                margin-top:5px;
                font-weight:600;
            }
            
        }
        .position {
            margin:3px 0 5px;
            font-size:0.75em;
            color: #9a9a9a;
            .lab {
                display:inline-block;
                font-size: 1.4em;
                color:#ccc;
                line-height: 0.9em;
                svg {
                font-size:17px;
                margin-left:4px;
                    vertical-align: bottom;
                }
            }
            .lab:hover {
                color:#F40019;
            }
        }
        .contact {
            font-size:0.72em;
            font-weight:500;
            line-height:120%;
            color:#707070;
        }
    }

